#navbar ul{position:fixed;top:0;width:100%;list-style-type:none;margin:0;padding:0;overflow:hidden;background-color:rgba(255,255,255,0.9);z-index:1101}#nav-links{float:right;margin:1.4rem}#nav-links li{float:left;margin:2px}#nav-links li a{display:block;color:#1A535C;text-align:center;padding:2px 16px 16px 16px;text-decoration:none;border:none}#nav-links li a:hover{background-color:#FF6B6B !important;color:#F7FFF7;-webkit-transition:0.2s ease-in;transition:0.2s ease-in}#nav-links li.active>a{background-color:#1A535C;color:#F7FFF7}



