@import url(./assets/css/normalize.min.css);@import url(./assets/css/skeleton.min.css);body{font-style:normal;font-weight:400;position:relative;background-color:#F7FFF7;color:#1A535C}h1{font-family:'Montserrat';text-align:center;font-weight:500}h1.hero-heading{font-size:100px}h4{font-size:2rem}h5{font-size:1.7rem}a{color:#4ECDC4;-webkit-transition:all 200ms ease;transition:all 200ms ease}::-moz-selection{text-shadow:none;background:rgba(78,205,196,0.25)}::selection{text-shadow:none;background:rgba(78,205,196,0.25)}.no-margin{margin:0}.no-padding{padding:0 !important}.align-right{text-align:right}.align-left{text-align:left}.button{border-radius:100px;margin-bottom:0}.button-standalone{margin:0 1rem}.button-primary{background-color:#4ECDC4 !important;border-color:#4ECDC4 !important}section{padding:8rem 0;text-align:center}.section-even{background-color:rgba(78,205,196,0.5);background-position:center;background-repeat:no-repeat}.section-heading,.section-description{margin-bottom:1.2rem}@media (min-width: 550px){.section{padding:12rem 0 11rem}.hero{padding-bottom:12rem;text-align:left;height:165px}.hero-heading{font-size:2.4rem}}@media (min-width: 750px){.hero{height:190px}.hero-heading{font-size:2.6rem}.section{padding:14rem 0 15rem}.hero{padding:16rem 0 14rem}.section-description{max-width:60%;margin-left:auto;margin-right:auto}.categories{padding:15rem 0 8rem}}@media (min-width: 1000px){.section{padding:20rem 0 19rem}.hero{padding:22rem 0}.hero-heading{font-size:3.0rem}}



