@-webkit-keyframes spinOut400{from{-webkit-transform:rotate(400deg);transform:rotate(400deg)}to{-webkit-transform:rotate(0deg);transform:rotate(0deg)}}@keyframes spinOut400{from{-webkit-transform:rotate(400deg);transform:rotate(400deg)}to{-webkit-transform:rotate(0deg);transform:rotate(0deg)}}@-webkit-keyframes spinOut300{from{-webkit-transform:rotate(300deg);transform:rotate(300deg)}to{-webkit-transform:rotate(0deg);transform:rotate(0deg)}}@keyframes spinOut300{from{-webkit-transform:rotate(300deg);transform:rotate(300deg)}to{-webkit-transform:rotate(0deg);transform:rotate(0deg)}}@-webkit-keyframes spinAround{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes spinAround{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.preload{-webkit-animation:none !important;animation:none !important}.b,.d{-webkit-transform-origin:40% 40%;transform-origin:40% 40%;display:inline-block}.b{-webkit-animation:spinOut300 0.3s linear;animation:spinOut300 0.3s linear}.d{-webkit-animation:spinOut400 0.3s linear;animation:spinOut400 0.3s linear}.logo{width:50px;height:50px;padding:0 !important}.logo:hover .b{-webkit-animation:spinAround 1.3s ease infinite;animation:spinAround 1.3s ease infinite}.logo:hover .d{-webkit-animation:spinAround 0.9s ease infinite;animation:spinAround 0.9s ease infinite}#logo{float:left;width:50px;height:50px;margin:1rem 0}#logo>a{width:50px;height:50px;padding:0 !important}



